*,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 14px;
}

:root {
    --color-primary: #524EEE;
    --color-secondary: #171717;
    --color-border: rgba(0, 0, 0, 0.1);
    --color-white: #fff;
    --color-title: #222222;
    --color-meta: rgba(23, 23, 23, 0.6);
}

.site-content {
    padding-top: 60px;
    overflow: hidden;
}

@media (max-width:991px) {
    .hidden-sm {
        display: none;
    }
}

@media (min-width:992px) {
    .hidden-md {
        display: none;
    }
}

@media (min-width:1200px) {
    .container {
        width: 1200px;
    }
}

@media (min-width: 768px) and (max-width:991px) {
    .container {
        width: 750px;
    }

    .container--fw {
        padding-left: 30px;
        padding-right: 30px;
    }
}

@media (min-width: 992px) and (max-width:1199px) {
    .container {
        width: 970px;
    }
}

@media (max-width: 767px) and (min-width: 576px) {
    .container {
        width: 540px;
    }
}

@media (max-width:575px) {
    .container {
        width: 100%;
    }
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
}

.container--narrow {
    max-width: 970px;
}

.module {
    display: block;
}

.module:not(:last-child) {
    margin-bottom: 80px;
}

.inverse-text .post__title,
.inverse-text a.post__cat,
.inverse-text .post__author .author-name,
.inverse-text .post__excerpt {
    color: #fff;
    color: var(--color-white);
}

@media (max-width:1199px) {
    .module:not(:last-child) {
        margin-bottom: 70px;
    }
}

@media (max-width:991px) {
    .module:not(:last-child) {
        margin-bottom: 60px;
    }
}

@media (max-width:575px) {
    .module:not(:last-child) {
        margin-bottom: 50px;
    }
}

