.post {
    position: relative;
}

/*---- Post thumb ----*/
.post__thumb,
.entry-thumb {
    position: relative;
}

.post__thumb img,
.entry-thumb img {
    display: block;
    width: 100%;
    height: auto;
}

.post__thumb a:not(.post__live),
.entry-thumb a:not(.post__live) {
    display: block;
}

.post__thumb--circle {
    border-radius: 50%;
    overflow: hidden;
}

.post__thumb--circle img {
    border-radius: 50%;
}

/*---- Post content ----*/
.post__text-inner {
    position: relative;
    padding: 20px;
}

.object-fit a:first-child,
.object-fit img {
    width: 100%;
    height: 100%;
}

.object-fit img {
    object-fit: cover;
}

.post--overlay .post__thumb {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.post--overlay .post__text {
    z-index: 1;
    position: relative;
}

.post--horizontal {
    display: flex;
    flex-direction: row;
}

.post--horizontal-reverse {
    flex-direction: row-reverse;
}

.post--horizontal .post__thumb {
    margin-right: 15px;
}

.post--horizontal-reverse .post__thumb {
    margin-left: 15px;
    margin-right: 0;
}

.post a {
    text-decoration: none;
}

.module__inner {
    position: relative;
}

.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
}

.text-right {
    text-align: right;
}

.site-content {
    padding: 35px 35px 60px 35px;
}

.line-clamp {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.line-clamp-1 {
    -webkit-line-clamp: 1;
}

.line-clamp-2 {
    -webkit-line-clamp: 2;
}

.line-clamp-3 {
    -webkit-line-clamp: 3;
}

@media (max-width:991px) {
    .site-content {
        padding: 30px 15px 100px 15px;
    }
}
.top-bar__inner {
