    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

.header-left .header-left__top .close:hover svg {
    -webkit-animation: cricle 2s linear infinite;
    animation: cricle 2s linear infinite;
}

@-webkit-keyframes cricle {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes cricle {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }

    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

