    position: absolute;
    content: "";
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 6px solid #4b4b4b;
    display: block;
    height: 0px;
    width: 0px;
    right: 10px;
    top: 50%;
    -webkit-transform: translateY(-50%);
